.Sponsor{
 
  margin: 5rem;
  transition: 0.3s;
}
.Sponsor:hover{
  transform: scale(1.1);
}
.Sponsor img{
    width: 25rem;
    display: inline-block;
    margin: 0;
}

.shead{
    font-size: 6rem;
    margin-bottom: 6rem;
   margin-top: -3rem;
}

.sponsorUS {
  margin-bottom: 7rem;
}

/* more smaller */
@media  (max-width: 570px){
  .shead{
    margin-top:0;
  }
  .Sponsor img{

  margin-bottom: 3rem;
}
}
